.image-tile {
    position: relative;
    display: flex;
    flex-direction: column;
    margin-top: 40px;

    @include ele(body) {
        display: flex;
        height: 150px; }

    @include ele(buttons) {
        margin-top: 5px; }

    @include ele(caption) {
        height: 43px;
        resize: none;
        margin-top: 4px; }

    @include ele(controls) {
        position: absolute;
        top: 0;
        right: 0; }

    @include ele(image) {
        display: flex; }

    @include ele(input) {
        margin: 0 15px 0 5px;
        padding-bottom: 0; }

    @include ele(name) {
        display: flex;
        align-items: center;
        margin-bottom: 4px; }

    @include ele(right) {
        margin-left: 15px;
        width: 100%; } }
