$leftMargin: 15px;

.flash-message {
    position: fixed;
    bottom: 20px;
    right: 30px;
    z-index: 100;
    background-color: $grey;
    width: 350px;
    height: 80px;
    color: $white;
    @include card(1);

    @include mod(error) {
        background-color: $red; }

    @include mod(success) {
        background-color: $green; }

    @include ele(bottom) {
        display: flex;
        margin-left: $leftMargin; }

    @include ele(close) {
        position: absolute;
        top: 5px;
        right: 5px;
        color: $white;
        cursor: pointer; }

    @include ele(lines) {
        & .lines svg {
            height: 20px; }

        & svg g {
            fill: $white; } }

    @include ele(title) {
        font-size: 1.7em;
        margin-left: 10px; }

    @include ele(top) {
        display: flex;
        align-items: center;
        margin: 10px 0 10px $leftMargin; } }

