$horizontalSpace: 15px;
$verticalSpace: 25px;
$infoSpace: 10px;

.dashboard {

    @include ele(arrows) {
        display: flex;
        flex-direction: column;
        justify-content: space-around; }

    @include ele(icon) {
        display: flex;
        align-items: center; }

    @include ele(item) {
        display: flex;
        margin: $verticalSpace 0;
        padding-bottom: $verticalSpace;
        border-bottom: $border;

        &:last-child {
            margin-bottom: 0; } }

    @include ele(info) {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        min-width: 220px;
        margin-right: $horizontalSpace; }

    @include ele(name) {
        margin: 0 0 $infoSpace; }

    @include ele(photo) {
        display: flex;
        margin-right: $horizontalSpace; }

    @include ele(visibility) {
        margin-bottom: $infoSpace; }

    @include ele(wrapper) {
        display: block; } }
