.project-form {
    @include ele(display-area) {
        position: absolute;
        margin: 0;
        padding: 5px 0 24px;

        & p:first-child {
            margin-top: 0; } }

    @include ele(display-filler) {
        height: 30px; }

    @include ele(section-controls) {
        text-align: right; }

    @include ele(preview) {
        text-align: center;
        font-size: 1.3em;
        margin-bottom: 4px;
        height: 20px; } }
