$formBorder: 1px solid $grey;
$formSectionSpace: 15px;

.form {
    color: $black;

    @include ele(button-row) {
        display: flex;
        justify-content: space-between;
        margin-right: 2px;

        & .button {
            margin-right: 10px; }

        & .button:last-child {
            margin-right: 0; } }

    @include ele(errors) {
        margin: $formSectionSpace 0;
        color: $red; }

    @include ele(input) {
        color: $black;
        width: calc(100% - 14px);
        padding: 5px;
        border: $formBorder; }

    @include ele(ghost-input) {
        background-color: $offWhite;
        border: none;
        padding-left: 0; }

    @include ele(label) {
        display: block;
        font-size: 1.1em;
        margin-bottom: 4px; }

    @include ele(section) {
        margin-bottom: $formSectionSpace; }

    @include ele(text) {
        min-height: 20px; }

    @include ele(textarea) {
        min-height: 150px;
        resize: none;

        @include mod(short) {
            min-height: 75px; }

        @include mod(flexible); }


    @include ele(errored) {
        border-color: $red;

        &:focus {
            outline: none !important;
            border: 1px solid $red;
            box-shadow: 0 0 4px $red; } } }
