.project-controls {
    display: flex;

    @include ele(group) {
        padding: 0 10px;
        border-right: $border;

        &:last-child {
            border-right: 0;
            padding-right: 0; } }

    @include ele(icon) {
        display: flex;
        align-items: center;
        cursor: pointer;
        margin-left: 5px;

        & g {
            transition: $hoverTransition; } }

    @include ele(section) {
        display: flex;
        align-items: center; }

    @include ele(red) {

        & g {
            fill: $red;

            &:hover {
                fill: $darkRed; } } }

    @include ele(green) {

        & g {
            fill: $green;

            &:hover {
                fill: $darkGreen; } } } }
