.project {

    @include ele(display-header) {
        margin: 0;
        color: $blue; }

    @include ele(display-item) {
        margin-left: 15px; }

    @include ele(display-section) {
        margin-bottom: 15px; }

    @include ele(error) {
        text-align: center; }

    @include ele(half) {
        flex: 1 0;
        width: 700px;

        &:first-child {
            padding-right: 20px;
            border-right: $border; }

        &:last-child {
            padding-left: 20px; } }

    @include ele(half-header) {
        text-align: center;
        font-style: italic;
        margin: 0 0 20px; }

    @include ele(main) {
        position: relative;
        display: flex;
        min-width: 1200px; } }
